.feature-bar{
    padding-top: 10px;
    background: #F7F7F7;
}

@media only screen and (max-width: 904px) {

    .feature-bar{
        padding-right: 2%;
        padding-left: 2%;
    }
}

.feature-bar-wrapper{
    margin: 0 auto;
    width: 1048px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.feature-bar-title {
    margin: 20px auto;
}

.feature-bar-title h2 {
    color: #3F7F9D;
    text-align: center;
    font-weight: 400;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 1.3px;
}

.feature-bar img{
    border-radius: 50%;
}

.feature-bar-item {

    float: none;
    width: auto;
    margin: 30px auto;
    display:-webkit-box;
    display:-webkit-flex;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
}

@media (min-width: $(tablet)){

    .feature-bar-item{
        width: 50%;
    }
}

@media (min-width: $(desktop)){

    .feature-bar-item{
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 1/3 - (30px - 30px * 1/3));
        float: left;
    }

    .feature-bar-item:nth-child(1n){
        margin-right: 30px;
    }

    .feature-bar-item:last-child{
        margin-right: 0;
    }

    .feature-bar-item:nth-child(3n){
        float: right;
    }

    .feature-bar-item:nth-child(3n){
        margin-right: 0;
    }
}

.feature-bar-item a{
    text-decoration: none;
}

.feature-bar-item a:hover{
    opacity: .75;
}

@media only screen and (max-width: 904px) {}

.feature-bar-image,
.feature-bar-body {
    display: table-cell;
    vertical-align: middle;

}

.feature-bar-image {
    padding-right: 10px;
}

.feature-bar-image > img {
    display: block;
    max-width: none;
}

.feature-bar-body h3 {
    font-size: 13px;
    font-weight: 400;
    text-decoration: underline;
    color: #3F7F9D;
}

.feature-bar-body p {
    font-family: "Open Sans";
    color: #4A4A4A;
}

.tab-links ul {
    display:-webkit-box;
    display:-webkit-flex;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
}

.tab-links li{
    margin:0 3px;
}

.tab-links li:nth-child(even){
    -webkit-box-ordinal-group:4;
    -webkit-order:3;
        -ms-flex-order:3;
            order:3;
}

.tab-links li:nth-child(odd){
    -webkit-box-ordinal-group:2;
    -webkit-order:1;
        -ms-flex-order:1;
            order:1;
}

.tab-links li:first-child{
    -webkit-box-ordinal-group:3;
    -webkit-order:2;
        -ms-flex-order:2;
            order:2;
}

.tab-gallery-tabs {
    margin: 0 auto;
    width:100%;
    margin-bottom:30px;
}

.tab-gallery-tabs ul {
    text-align: center;
    height: 80px;
}

.tab-gallery-tabs ul li {
    display: inline-block;
    font-family: "Open sans";
    border: 2px solid $purple;
    color: $purple;
    text-transform: uppercase;
    position:relative;
    text-align:center;
}

.tab-gallery-tabs ul li.active{
    background: $purple;
}

.tab-gallery-tabs ul li.active:hover:before {
    border-top-color:$purple_light;
}

.tab-gallery-tabs ul li.active a {
    color: #fff;
}

.tab-gallery-tabs ul li.active:before{
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    margin-top: 80px;
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-top: 14px solid $purple;
    left: 50%;
    margin-left: -7px;
}

.tab-gallery-tabs ul li a {
    text-decoration: none;
    line-height: 80px;
    padding: 30px 40px;
    color: $purple_light;
}

.tab-gallery-tabs ul li a:active{
    background: #FFF;
}

.tab-gallery-tabs ul li a:hover{
    background:$purple_light;
    color: white;
}

.tab-gallery-tabs ul li a:hover:before {
    border-top-color:$purple_light;
}

/* Style the select menu for mobile. Remove media queries once in production */

.tab-gallery-select{
    margin: 0 auto;
    max-width: 350px;
}

@media only screen and (max-width: 580px) {
    .tab-gallery-tabs ul {
        height: auto;
        width: 90%;
        margin: 0 auto;
    }
    .tab-gallery-tabs ul li  {
        display: block;
    }
}

.tab-gallery-wrapper{
    margin-top: 50px;
    margin-bottom: 50px;
}

.tab-gallery-wrapper img{
    margin: 0 auto;
    display: block;
    max-width:100%;
    height:auto;
}

.tab-gallery-wrapper .title {
    text-align:center;
}

@media (min-width: $(desktop)){

    .tab-gallery-wrapper .title{
        display: none;
    }
}

.tab-gallery-wrapper .tab-gallery-images {
    position:relative;
    width:100%;
}

.tab-gallery-wrapper .tab-gallery-images-wrapper{
    width:100%;
}

.tab-gallery-wrapper .tabs {
    width:100%;
}

.tab-gallery-button-wrapper{
    display: block;
    text-align: center;
    margin: 20px auto;
}

.tab-gallery-button-wrapper .button {
    margin: 20px 0;
}

/* Hide & show select menu and tabs based on screen width. Remove once in production */

@media only screen and (max-width: 904px) {
    .tab-gallery-tabs{
        display: none;
    }
}

@media only screen and (min-width: 904px) {
    .tab-gallery-select{
        display: none;
    }
}
